import { Pipe } from "@angular/core";
import { I18nService } from "app/services/i18n.service";

@Pipe({
    name: "i18n",
    pure: false
})

export class I18nPipe {
    constructor(private i18n: I18nService) {
    }

    transform(key: string) {
        return this.i18n.translate(key);
    }
}