import { Injectable } from "@angular/core";
import { Headers, Http, Response } from "@angular/http";

@Injectable()
export class I18nService {

    private mapLang: Map<string, string>;
    private userLang: string;

    constructor(private http: Http) {
        this.mapLang = new Map<string, string>();
        this.setUserLang("en_US");
        //this.setUserLang("vi_VN");
        this.loadUserLang();
    }

    private loadLang(): Promise<Object> {
        let headers = new Headers();
        let url = "/assets/lang/messages_" + this.userLang + ".json?t=" + Date.now();
        return this.http
            .get(url)
            .toPromise()
            .then(res => res.json())
            .catch((err) => err);
    }

    loadUserLang(): void {
        this.loadLang().then(data => {
            for (let k in data) {
                this.mapLang.set(k, data[k]);
            }
        }).catch(err => console.log(err));
    }

    setUserLang(lang: string): void {
        this.userLang = lang;
    }

    translate(key: string): string {
        if (this.mapLang.has(key)) {
            return this.mapLang.get(key);
        }
        return key;
    }
}
