import { Component, OnInit } from "@angular/core";

import { PopupService } from "app/services/popup.service";
import { SingleCharacterService } from "app/services/single-character.service";
import { SingleCharacter } from "app/models/single-character.model";
import { Pagination } from "app/models/pagination.model";
import { StoreUtil } from "app/utils/store.util";
import { PubSubService } from "app/services/pub-sub.service";
import { StringUtil } from "app/utils/string.util";
import { I18nService } from "app/services/i18n.service";

@Component({
    selector: "dashboard",
    templateUrl: "dashboard.component.html",
    styleUrls: [],
})
export class DashBoardComponent implements OnInit {
    public characters: SingleCharacter[] = [];
    public pagination: Pagination;
    private total: number;
    private isPending: boolean;
    private page: number;

    constructor(
        private popupService: PopupService,
        private singleCharacterService: SingleCharacterService,
        private storeUtil: StoreUtil,
        private stringUtil: StringUtil,
        private pubSubService: PubSubService,
        private i18nService: I18nService
    ) { }

    ngOnInit(): void {
        let page = this.storeUtil.cache.get('dash-board:page');
        this.page = page ? + page : 0;

        this.pagination = new Pagination();
        this.pagination.pageSize = 20;
        this.pagination.currentPage = -1;
        this.pagination.maxNumOfPages = 7;
        this.pagination.endPage = this.pagination.maxNumOfPages - 1;

        this.loadPage(this.page);
        this.pubSubService.publish(PubSubService.TOPICS.NAVIGAION_TAB_CHANGE, 'menu.items.home');
    }

    loadPage(page: number, forceLoad: boolean = false) {
        if (this.isPending) {
            return;
        }
        this.isPending = true;
        if (forceLoad === true || this.pagination.currentPage !== page) {
            this.singleCharacterService.findCommonCharacters(page, this.pagination.pageSize).then(hitList => {
                this.page = page;
                this.total = hitList.total;
                this.characters = hitList.hits;
                this.pagination.numOfPages = Math.floor(hitList.total / this.pagination.pageSize) + 1;
                if (this.pagination.numOfPages < this.pagination.maxNumOfPages) {
                    this.pagination.endPage = this.pagination.numOfPages - 1;
                }
                this.pagination.currentPage = page;
                this.storeUtil.cache.set('dash-board:page', page);
            }).catch(err => {
                console.log("Load common characters fail");
                this.popupService.showPopup(
                    this.i18nService.translate("msg.title.load_data"),
                    this.i18nService.translate("msg.err.load_data_fail")
                );
            }).then(() => this.isPending = false);
        }
    }

    detectPrePagination(e) {
        let p = this.pagination.detectPrePagination();
        if (p !== this.pagination) {
            this.pagination = p;
            this.loadPage(this.pagination.currentPage, true);
        }
    }

    detectNextPagination(e) {
        let p = this.pagination.detectNextPagination();
        if (p !== this.pagination) {
            this.pagination = p;
            this.loadPage(this.pagination.currentPage, true);
        }
    }

    prettyDefinition(s: string) {
        let html = '';
        if (s) {
            let ulOpen = false;
            let lines = s.split('\n')
            .filter(t => !!t && t.length > 0)
            .map(t => {
                //add link for each Chinese character
                let r = '';
                for (let i = 0, n = t.length; i < n; i++) {
                    if(this.stringUtil.checkChineseCharacter(t.charAt(i))) {
                        r += `<a href="#/character/${t.charAt(i)}">${t.charAt(i)}</a>`;
                    } else {
                        r += t.charAt(i);
                    }
                }
                return r;
            });
            for (let i = 0, n = lines.length; i < n; i++) {
                if (/^\s+/.test(lines[i])) {
                    if (!ulOpen) {
                        html += '<ul>';
                        ulOpen = true;
                    }
                    html += `<li>${lines[i]}</li>`;
                } else {
                    if (ulOpen) {
                        html += '</ul>';
                        ulOpen = false;
                    }
                    html += `<p>${lines[i]}</p>`;
                }
            }
        }
        return html;
    }
}
